@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap);
.NavbarItems {
    color: #fff;
    height: 8vh;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    font-size: 1rem;
    margin:  0;

}


.NavbarItems .navbar-logo {
    color: rgb(0, 0, 0);
    justify-self: start;
    margin-left: 20px;
    cursor: pointer;
    opacity: 1;
    width: 150px;
   
}

.NavbarItems .logohref
{
    text-decoration: none;
    opacity: 1;
}
.NavbarItems .nav-menu{
    display: grid;
    grid-template-columns: repeat(4, auto);
    grid-gap: 10px;
    list-style: none;
    text-align: center;
    width: 70vw;
    -webkit-justify-content: end;
            justify-content: end;
    margin-right: 1rem;
    margin-bottom: 1rem;
    font-weight: 1000;
    font-size: 1rem;
}

.NavbarItems .nav-links {
    color: rgb(0, 0, 0);
    text-decoration: none;
    padding: 0.5rem 1rem;
}

.NavbarItems .nav-links:hover {
    background-color: #6d76f7;
    border-radius: 4px;
    transition: all 0.2s ease-out;
}

.NavbarItems .fa-bars {
    color: rgb(138, 138, 138);
    top: 1000px;
}

.NavbarItems .nav-links-mobile {
    display: none;
}

.NavbarItems .menu-icon {
    display: none;
}


@media screen and (max-width: 960px) {
    .NavbarItems {
        position: relative;
    }

    .NavbarItems .nav-menu {
        display: -webkit-flex;
        display: flex;
        -webkit-flex-direction: column;
                flex-direction: column;
        width: 97vw;
        position: absolute;
        top: -800px;
        margin: 3px;
        transition: all 0.7s ease-out;
        font-size: 1rem;
        padding :0;   
    }

    .NavbarItems .nav-menu.active {
        background:rgb(151, 151, 151) ;
        opacity:0.9;
        top: 60px; 
        transition: all 0.7s  ease-out;
        z-index: 1;
        font-style: italic;
        font-size: 18px;
        font-weight: 1000;
    }

    .NavbarItems .nav-links {
        text-align: center;
        margin-left: 50px;
        margin-top: 10px;
        padding: 10px;
        width: 70vw;
        display: table;
        
    }

    .NavbarItems .nav-links:hover {
        background-color: #7577fa;
        border-radius: 0;
    }

    .NavbarItems .navbar-logo {
        position: absolute;
        top: 0;
        left: 0;
    }

    .NavbarItems .menu-icon {
        display: block;
        position: absolute;
        top:-5px;
        right: 0;
        -webkit-transform: translate(-100%, 60%);
                transform: translate(-100%, 60%);
        font-size: 1.8rem;
        cursor: pointer;
    }

    .NavbarItems .fa-times {
        color: rgb(138, 138, 138);
        font-size: 2rem;

    }

    .NavbarItems .nav-links-mobile {
        display: block;
        text-align: center;
        padding: 10px;
        margin-left: 50px;
        border-radius: 4px;
        width: 70%;
        background: #4ad9e4;
        text-decoration: none;
        color: #fff;
        font-size: 1.1rem; 
        margin-bottom: 20px ;
     }

    .NavbarItems .nav-links-mobile:hover {
        background: #fff;
        color: #6568F4;
        transition: 250ms;
        font-weight: 1000;
    }

    .NavbarItems Button {
        display: none;
    }
}
